export default "\nimport CursorInput from 'component/CursorInput'\nimport { useState } from 'react'\nimport styles from './index.less'\nconst CursorInputBaseView = () => {\n    const [selectedOptions, setSelectedOptions] = useState([\n        {\n            key: 1,\n            content: '李白'\n        },\n        {\n            key: 2,\n            content: '赵云',\n        },\n        {\n            key: 3,\n            content: '王昭君',\n        }\n    ])\n    \n    return (\n        <div>\n            <CursorInput  selectedOptions={selectedOptions} setSelectedOptions={setSelectedOptions} />\n        </div>\n    )\n}\n\nexport default CursorInputBaseView;";